.skillsBlock {
  padding: 60px 0;

  .skillsContainer {
    justify-content: flex-start;

    .skills {
      margin-top: 50px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      row-gap: 20px;
      border: 1px solid black;
      width: 100%;

    }
  }
  @media screen and (max-width: 600px){
      padding-bottom: 30px;
  }
}



