@import "src/common/styles/sass/variables";
@import "src/common/styles/sass/mixins";

.skill {
  background-color: $bgSecondColor;
  width: 200px;
  height: 140px;
  display: flex;
  row-gap: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  @media screen and (max-width: 1150px){
    width: 200px;
    height: 140px;
  }
  @media screen and (max-width: 1050px){
    width: 180px;
    height: 120px;
  }
  @media screen and (max-width: 700px){
    width: 140px;
    height: 100px;
  }
  @media screen and (max-width: 560px){
    width: 120px;
    height: 80px;
  }

  .icon {
    & img {
      height: 50px;
      width: 100%;
      @media screen and (max-width: 1050px){
        height: 40px;
      }
      @media screen and (max-width: 700px){
        height: 35px;
      }
    }
  }
  .title {
    @include poppinsText(27px, 500, $mainTextColor);
    @media screen and (max-width: 1050px){
      font-size: 22px;
    }
    @media screen and (max-width: 700px){
      font-size: 16px;
    }
    @media screen and (max-width: 560px){
      display: none;
    }
  }

}


